import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import Layout from "@/components/Layout";
import { appConstant } from "@/constants/app";

Vue.use(VueRouter);

export default new VueRouter({
  // mode: "history",
  routes: [
    {
      path: "/",
      name: "Home",
      component: Home,
    },
    {
      path: "/about",
      name: "About",
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () => import(/* webpackChunkName: "about" */ "../views/About.vue"),
    },
    {
      path: "/seat_edit",
      name: "SeatEdit",
      component: () => import("@/components/FloorPlan/SeatEdit/index.vue"),
    },
    {
      path: "/pricing_edit",
      name: "pricing_edit",
      component: () => import("@/components/Pricing/PricingEdit/index.vue"),
    },
    {
      path: "/test",
      name: "test",
      component: () => import("@/components/Pricing/PricingEdit/test.vue"),
    },
    {
      path: "",
      component: Layout,
      name: "seat",
      meta: { title: "seat", icon: "seat", menu: appConstant.MENU.PRICING },
      children: [
        {
          path: "pricing",
          redirect: "./",
        },
        {
          path: "floor_plan",
          redirect: "./",
          component: () => import("@/components/Layout"),
          name: "Layout",
          meta: { title: "layout", noCache: true, menu: appConstant.MENU.PRICING },
        },
      ],
    },
  ],
});
